*{padding: 0; margin: 0;}
a{
    text-decoration: none !important;
}


.navbar{
    background-color: #026EA4 !important;
    height: 8vh;
    top: 0 !important;
    padding: 0 10px;
}

.navbar-collapse{
    background-color: #026EA4 !important;

}


@media  (max-width: 600px) {
    .navbar-collapse{
        background-color: #026EA4 !important;
        z-index: 11111 !important;
        /* display: flex !important; */
        justify-content: space-between !important; 

    }
    .navbar-nav{
        padding: 10px;
    }
    
  }


